import React from "react";
import { Box, Button, Collapse, IconButton, Table, TableBody, TableCell, TableHead, TableRow, Typography } from "@mui/material"

import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';

import axios from "axios";
import { green } from "@mui/material/colors";

export const Linha = ({ row, labelId, dense, largura, serVice, setVeiculo }) => {

    const [open, setOpen] = React.useState(false)

    // console.log(row.servicoSolicitado)

    const funcao = () => {
        let passo = false
        serVice(passo)
        setVeiculo(row)
    }


    return (
        <>
            <TableRow
                key={row._id}
                hover
            >
                {largura > 799 ?
                    <>
                        <TableCell sx={{ '& > *': { borderBottom: 'unset' } }}>
                            {row.situacaoServico === "NÃO SOLICITADO" &&
                                <Button onClick={funcao}>
                                    cadastrar cliente
                                </Button>}
                            {row.situacaoServico === 'EM PROCESSO' &&
                                <Button color="success">
                                    Em Processo
                                </Button>}
                            {row.situacaoServico === 'SERVICO SOLICITADO' &&
                                <Button color="success">
                                    Servico Solicitado
                                </Button>}
                        </TableCell>
                        <TableCell
                            component="th"
                            id={labelId}
                            scope="row"
                            padding="none"
                        >
                            {row.veiculo}
                        </TableCell>
                        <TableCell align="right">{row.placa}</TableCell>
                        <TableCell align="right">{row.chassi}</TableCell>
                        {largura > 799 && <TableCell align="right">{row.proprietario}</TableCell>}
                        {largura > 799 && <TableCell align="right">{row.situacaoServico}</TableCell>}
                        {largura > 799 && <TableCell align="right">{row.situacao}</TableCell>}
                    </>
                    :
                    <>
                        <TableCell
                            component="th"
                            id={labelId}
                            scope="row"
                        >
                            {row.veiculo}
                        </TableCell>
                        <TableCell align="right">{row.placa}</TableCell>
                    </>
                }
                <TableCell>
                    <IconButton
                        aria-label="expand row"
                        size="small"
                        onClick={() => setOpen(!open)}
                    >
                        {open ? <KeyboardArrowUpIcon color='primary' /> : <KeyboardArrowDownIcon color='primary' />}
                    </IconButton>
                </TableCell>
            </TableRow>
            <TableRow sx={{ '& > *': { borderBottom: 'unset', bgcolor: 'rgba(200, 200, 200, 0.2)' } }} >
                <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={10} >
                    <Collapse in={open} timeout="auto" unmountOnExit>
                        <Box sx={{ margin: 1 }}>
                            <Typography color='primary' variant="h6" gutterBottom component="div" >
                                Detalhes
                            </Typography>
                            <Typography component='div' color={green[800]} sx={{ m: 2, fontSize: 20 }}>
                                Informações complementares do veículo
                            </Typography>

                            {largura > 799 ?
                                <>
                                    <Table sx={{ minWidth: 200 }} size={dense ? 'small' : 'medium'} aria-label="purchases" >
                                        <TableHead>
                                            <TableRow>
                                                <TableCell sx={{ width: '25%' }}>Motor</TableCell>
                                                <TableCell >{row.motor}</TableCell>
                                                <TableCell>Ano de Fabricação</TableCell>
                                                <TableCell>{row.anoFabricacao}</TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody >
                                            <TableRow >
                                                <TableCell >Cidade</TableCell>
                                                <TableCell >{row.municipio}</TableCell>
                                                <TableCell >Ano do Modelo</TableCell>
                                                <TableCell >{row.anoModelo}</TableCell>
                                            </TableRow>
                                        </TableBody>
                                        <TableHead>
                                            <TableRow>
                                                <TableCell>UF</TableCell>
                                                <TableCell>{row.uf}</TableCell>
                                            </TableRow>
                                        </TableHead>
                                    </Table>
                                    <Typography component='div' color={green[800]} sx={{ m: 2, fontSize: 20 }}>Informações do cadastro do veículo</Typography>
                                    <Table>
                                        <TableHead>
                                            <TableRow>
                                                <TableCell >Veículo Cadastrado Por</TableCell>
                                                <TableCell >{row.cadastradoPor.nome}</TableCell>
                                                <TableCell >Veículo Cadastrado Em</TableCell>
                                                <TableCell >{row.cadastradoPor.Data}</TableCell>
                                            </TableRow>
                                        </TableHead>
                                    </Table>
                                    {row.servicoSolicitado ?
                                        null
                                        :
                                        <>
                                            <Typography color={green[800]} sx={{ m: 2, fontSize: 20 }}>Serviço</Typography>
                                            <Table>
                                                <TableBody >
                                                    <TableRow >
                                                        <TableCell>Situação</TableCell>
                                                        <TableCell>{row.situacaoServico}</TableCell>
                                                        <TableCell >Cadastrado em</TableCell>
                                                        <TableCell >{row.cadastradoPor.Data}</TableCell>
                                                    </TableRow>
                                                </TableBody>
                                                <TableHead>
                                                    <TableRow>
                                                        <TableCell >Serviço</TableCell>
                                                        <TableCell >{row.situacao}</TableCell>
                                                        <TableCell >GRT</TableCell>
                                                        <TableCell >{row.grtUtilizada}</TableCell>
                                                    </TableRow>
                                                </TableHead>
                                            </Table>
                                        </>
                                    }
                                </>
                                :
                                <Table>
                                    <TableHead>
                                        <TableRow>
                                            <TableCell>Motor</TableCell>
                                            <TableCell component="th" scope="row">{row.motor}</TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody >
                                        <TableRow >
                                            <TableCell>Ano de Fabricação</TableCell>
                                            <TableCell>{row.anoFabricacao}</TableCell>
                                        </TableRow>
                                    </TableBody>
                                    <TableHead>
                                        <TableRow>
                                            <TableCell>UF</TableCell>
                                            <TableCell component="th" scope="row">{row.uf}</TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody >
                                        <TableRow >
                                            <TableCell>Serviço</TableCell>
                                            <TableCell>{row.situacaoServico}</TableCell>
                                        </TableRow>
                                    </TableBody>
                                    <TableBody >
                                        <TableRow >
                                            <TableCell>Serviço Solicitado</TableCell>
                                            <TableCell component="th" scope="row">{row.servicoSolicitado}</TableCell>
                                        </TableRow>
                                    </TableBody>
                                    <TableHead>
                                        <TableRow>
                                            <TableCell>Solicitante do Serviço</TableCell>
                                            <TableCell>
                                                {
                                                    row.pai ?
                                                        axios.get("http://192.168.15.49:3005/Consulta/Pai")
                                                            .then(res => console.elog(res))
                                                            .catch(err => console.log(err))
                                                        :
                                                        "Não possui"
                                                }
                                            </TableCell>
                                        </TableRow>
                                    </TableHead>
                                </Table>

                            }
                        </Box>
                    </Collapse>
                </TableCell>
            </TableRow >
        </>
    )
}