import React from "react";
import { Alert, Button, Checkbox, FormControl, FormControlLabel, Input, InputAdornment, Paper, Snackbar, Typography } from "@mui/material"
import style from './SolicitaServico.module.scss';
import { Box } from "@mui/system";
import moment from 'moment';

export const SolicitaServico = ({ user, veiculo, setCadastroServico }) => {

    const [despachante, setDespachante] = React.useState({
        nome: user.nome,
        rg: user.rg,
        cpf: user.cpf,
        crdd: user.nCrddrs,
        logradouro: user.logradouro,
        cidade: user.cidade,
    })

    const [servico, setServico] = React.useState({
        inclusao_ou_alteracao_registro_financeiro: false,
        agente_financeiro: '',
        alteracao_info_proprietario: false,
        alteracao_info_veiculo: false,
        alteracao_restricao_financeira: false,
        alteracao_endereco_entrega_CRL_CRLV: false,
        correcao_restricao: false,
        emissao_DCPPO: false,
        fornecimento_placa_experiencia_fabricante: false,
        inclusao_restricao_financeira: false,
        liberacao_averbacao_execucao: false,
        liberacao_restricao_financeira: false,
        licenca_especial_transito: false,
        mudanca_placa_unica: false,
        primeiro_emplamaneto: false,
        renovacao_placa_experiencia_fabricante: false,
        reserva_placa: false,
        restricao_transferencia: false,
        solicitacao_autorizacao_alteracao_caracteristicas: false,
        solicitacao_autorizacao_regravacao_chassi: false,
        solicitacao_autorizacao_transporte_escolar: false,
    })

    const [novoEndereco, setNovoEndereco] = React.useState({
        novo_endereco: "",
        numero: "",
        bairro: "",
        cidade: "",
        uf: "",
        cep: ""
    })

    const [servico2, setServico2] = React.useState({
        averbacao_execucao: false,
        baixa_placa_experiencia_fabricante: false,
        baixa_outra_uf: false,
        baixa_veiculo: false,
        cancelamento_processo: false,
        colocacao_lacre_placa: false,
        comunicacao_venda: false,
        correcao_observacao_CRV_CRLV: false,
        correcao_chassi: false,
        correcao_info_proprietario: false,
        correcao_info_veiculo: false,
        correcao_municipio: false,
        autorizacao_detran: false,
        nao_autorizacao_detran: false,
        solicitacao_certidao: false,
        solicitacao_copia_documento: false,
        solicitacao_copia_CRLV: false,
        solicitacao_vistoria: false,
        solicitacao_DCPPO: false,
        transferencia_propriedade_veiculo_outro_uf: false,
        transferencia_propriedade_veiculo: false,
        troca_municipio_veiculo_outro_uf: false,
        troca_municipio_veiculo: false,
        outros: false,
        outros_entradas: ""
    })

    const [veic, setVeic] = React.useState({
        ao_veiculo: veiculo.data.modelo,
        chassi_ou_placa: veiculo.data.chassi
    })

    const [validaSeg, setValidaSeg] = React.useState({
        CRLV: false,
        CRV_CRLV: false,
        motivo: "",
    })

    const [novoEnderecoResidencial, setNovoEnderecoResidencial] = React.useState({
        altera_end: false,
        endereco: "",
        cep: ""
    })

    const segundaVia = (e) => {
        const valida = e.target.id;
        valida === '2via_CRLV' && setValidaSeg(a => ({ ...a, CRLV: e.target.checked, CRV_CRLV: false }))
        valida === '2via_CRV_CRLV' && setValidaSeg(a => ({ ...a, CRLV: false, CRV_CRLV: e.target.checked }))
        validaSeg.CRLV === false ? validaSeg.CRV_CRLV === false ? setValidaSeg(a => ({ ...a, motivo: "" })) : setValidaSeg(a => ({ ...a, motivo: "" })) : setValidaSeg(a => ({ ...a, motivo: "" }))
    }

    const [d, setD] = React.useState(moment().format('YYYY-MM-DD'))

    const alteraEndereco = (e) => {
        setServico(a => ({ ...a, alteracao_endereco_entrega_CRL_CRLV: e.target.checked }))
        setNovoEndereco(b => ({
            ...b,
            novo_endereco: "",
            numero: "",
            bairro: "",
            cidade: "",
            uf: "",
            cep: ""
        }))
    }
    const registroFinanceiro = (e) => {
        setServico(a => ({ ...a, inclusao_ou_alteracao_registro_financeiro: false, agente_financeiro: '' }))
    }

    const Enviar = (e) => {
        e.preventDefault();
        try {
            //     console.log(
            //     despachante,
            //     servico,
            //     novoEndereco,
            //     servico2,
            //     veic,
            //     validaSeg,
            //     novoEnderecoResidencial
            // )
            setCadastroServico(true)
            avisoPositivo()
        }
        catch {
            avisoNegativo()
        }
    }

    const [op, setOp] = React.useState({
        op: false,
        tipo: 'info',
        frase: ''
    })
    const fecharAviso = () => { setOp(a => ({ ...a, op: false })) }
    const avisoPositivo = () => {
        setOp(a => ({
            ...a,
            op: true,
            tipo: 'success',
            frase: 'Cadastro da solicitação realizado com sucesso!'
        }))
    }
    const avisoNegativo = () => {
        setOp(a => ({
            ...a,
            op: true,
            tipo: 'error',
            frase: 'Cadastro da solicitação não realizado!'
        }))
    }


    return (
        <>
            <Snackbar
                open={op.op}
                autoHideDuration={6000}
                onClose={fecharAviso}
            >
                <Alert onClose={fecharAviso} severity={op.tipo} sx={{ width: '100%' }} >
                    {op.frase}
                </Alert>
            </Snackbar>
            <form className={style.opcoes} onSubmit={Enviar}>
                <Box sx={{ width: '100%' }}>
                    <Paper sx={{ p: 2 }}>
                        <Typography variant="h5" align="center">GRT - GUIA DE RESPONSABILIDADE TÉCNICA</Typography>
                        <div className={style.boxlinha}>
                            <h2>Despachante / Auxiliar(DAV):</h2>
                            <FormControl sx={{ width: '40%' }} variant="standard" >
                                <Input
                                    value={despachante.nome}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                    onChange={(e) => setDespachante(a => ({ ...a, nome: e.target.value }))}
                                />
                            </FormControl>
                            <h2>RG:</h2>
                            <FormControl sx={{ width: '20%' }} variant="standard" >
                                <Input
                                    value={despachante.rg}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                    onChange={(e) => setDespachante(a => ({ ...a, rg: e.target.value }))}
                                />
                            </FormControl>
                            <h2>CPF:</h2>
                            <FormControl sx={{ width: '20%' }} variant="standard" >
                                <Input value={despachante.cpf}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                    onChange={(e) => setDespachante(a => ({ ...a, cpf: e.target.value }))}
                                />
                            </FormControl>
                        </div>
                        <div className={style.boxlinha}>
                            <h2>CRDD Nº:</h2>
                            <FormControl sx={{ width: '13%' }} variant="standard" >
                                <Input value={despachante.crdd}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                    onChange={(e) => setDespachante(a => ({ ...a, crdd: e.target.value }))}
                                />
                            </FormControl>
                            <h2>,Endereço:</h2>
                            <FormControl sx={{ width: '40%' }} variant="standard" >
                                <Input value={despachante.logradouro}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                    onChange={(e) => setDespachante(a => ({ ...a, logradouro: e.target.value }))}
                                />
                            </FormControl>
                            <h2>Cidade:</h2>
                            <FormControl sx={{ width: '32%' }} variant="standard" >
                                <Input value={despachante.cidade}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                    onChange={(e) => setDespachante(a => ({ ...a, cidade: e.target.value }))}
                                />
                            </FormControl>
                        </div>
                        <h2 className={style.linhaservico}>Requer ao DETRAN/RS a realização dos seguintes serviços</h2>
                        <div className={style.container}>
                            <div className={style.boxleft}>
                                <h2>Haverá inclusão ou alteração de registro financeiro?</h2>
                                <div className={style.unicoassim}>
                                    <div>
                                        <FormControlLabel control={
                                            <Checkbox
                                                checked={servico.inclusao_ou_alteracao_registro_financeiro}
                                                onChange={(e) => setServico(a => ({ ...a, inclusao_ou_alteracao_registro_financeiro: true }))} />}
                                            label='Sim'
                                        />
                                    </div>
                                    <div>
                                        <FormControlLabel control={
                                            <Checkbox
                                                checked={!servico.inclusao_ou_alteracao_registro_financeiro}
                                                onChange={registroFinanceiro} />}
                                            label="Não"
                                        />
                                    </div>
                                </div>
                                <div className={style.boxlinha}>
                                    <h2>Agente Financeiro</h2>
                                    <FormControl
                                        disabled={!servico.inclusao_ou_alteracao_registro_financeiro}
                                        fullWidth variant="standard" >
                                        <Input
                                            required
                                            value={servico.agente_financeiro}
                                            onChange={(e) => setServico(a => ({ ...a, agente_financeiro: e.target.value }))}
                                            startAdornment={<InputAdornment position="start"></InputAdornment>}
                                        />
                                    </FormControl>
                                </div>
                                <div className={style.boxlinha}>
                                    <FormControlLabel control={<Checkbox id='2via_CRLV' checked={validaSeg.CRLV} onChange={segundaVia} />} label='2ª Via CRLV' />
                                    <FormControlLabel control={<Checkbox id='2via_CRV_CRLV' checked={validaSeg.CRV_CRLV} onChange={segundaVia} />} label='2ª Via CRV/CRLV' />
                                </div>
                                <div className={style.boxlinha}>
                                    <h2>Motivo:</h2>
                                    <FormControl
                                        disabled={validaSeg.CRLV === false ? validaSeg.CRV_CRLV === false ? true : false : false}
                                        fullWidth variant="standard" >
                                        <Input
                                            required
                                            value={validaSeg.motivo}
                                            onChange={(e) => setValidaSeg(a => ({ ...a, motivo: e.target.value }))}
                                            startAdornment={<InputAdornment position="start"></InputAdornment>}
                                        />
                                    </FormControl>
                                </div>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={novoEnderecoResidencial.altera_end}
                                            onChange={(e) => setNovoEnderecoResidencial(a => ({
                                                ...a,
                                                altera_end: e.target.checked,
                                                endereco: novoEnderecoResidencial.altera_end === false ? '' : '',
                                                cep: novoEnderecoResidencial.altera_end === false ? '' : '',
                                            }))}
                                        />}
                                    label='Alteração do endereço residencial' />
                                <div className={style.boxlinha}>
                                    <h2>Novo endereço:</h2>
                                    <FormControl
                                        disabled={!novoEnderecoResidencial.altera_end}
                                        fullWidth variant="standard" >
                                        <Input
                                            required
                                            value={novoEnderecoResidencial.endereco}
                                            onChange={(e) => setNovoEnderecoResidencial(a => ({ ...a, endereco: e.target.value }))}
                                            startAdornment={<InputAdornment position="start"></InputAdornment>}
                                        />
                                    </FormControl>
                                </div>
                                <div className={style.boxlinha}>
                                    <Box sx={{ mb: 2, display: 'flex', alignItems: 'center', width: '100%' }}>
                                        <h2>CEP:</h2>
                                        <FormControl
                                            disabled={!novoEnderecoResidencial.altera_end}
                                            fullWidth variant="standard" >
                                            <Input

                                                required
                                                value={novoEnderecoResidencial.cep}
                                                onChange={(e) => setNovoEnderecoResidencial(a => ({ ...a, cep: e.target.value }))}
                                                startAdornment={<InputAdornment position="start"></InputAdornment>}
                                            />
                                        </FormControl>
                                    </Box>
                                </div>
                                <div className={style.coluna}>
                                    <FormControlLabel
                                        sx={{ height: 22 }}
                                        control={
                                            <Checkbox
                                                size='small'
                                                checked={servico.alteracao_info_proprietario}
                                                onChange={(e) => setServico(a => ({ ...a, alteracao_info_proprietario: e.target.checked }))}
                                            />}
                                        label='Alteração de Informações do Proprietário' />
                                    <FormControlLabel
                                        sx={{ height: 22 }}
                                        control={
                                            <Checkbox
                                                size='small'
                                                checked={servico.alteracao_info_veiculo}
                                                onChange={(e) => setServico(a => ({ ...a, alteracao_info_veiculo: e.target.checked }))}
                                            />}
                                        label='Alteração de Informações do Veículo' />
                                    <FormControlLabel
                                        sx={{ height: 22 }}
                                        control={
                                            <Checkbox
                                                size='small'
                                                checked={servico.alteracao_restricao_financeira}
                                                onChange={(e) => setServico(a => ({ ...a, alteracao_restricao_financeira: e.target.checked }))}
                                            />}
                                        label='Alteração de Restrição Financeira' />
                                    <FormControlLabel
                                        sx={{ height: 22 }}
                                        control={
                                            <Checkbox
                                                size='small'
                                                checked={servico.alteracao_endereco_entrega_CRL_CRLV}
                                                onChange={alteraEndereco}
                                            />}
                                        label='Alteração de Endereço de entrega do CRV/CRLV' />
                                </div>
                            </div>
                            <div className={style.boxrigth}>
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.correcao_restricao}
                                            onChange={(e) => setServico(a => ({ ...a, correcao_restricao: e.target.checked }))}
                                        />}
                                    label='Correção de Restrições'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.emissao_DCPPO}
                                            onChange={(e) => setServico(a => ({ ...a, emissao_DCPPO: e.target.checked }))}
                                        />}
                                    label='Emissão de DCPPO'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.fornecimento_placa_experiencia_fabricante}
                                            onChange={(e) => setServico(a => ({ ...a, fornecimento_placa_experiencia_fabricante: e.target.checked }))}
                                        />}
                                    label='Fornecimento de Placa de Experiência ou de Fabricante'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.inclusao_restricao_financeira}
                                            onChange={(e) => setServico(a => ({ ...a, inclusao_restricao_financeira: e.target.checked }))}
                                        />}
                                    label='Inclusão de Restrição Financeira'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.liberacao_averbacao_execucao}
                                            onChange={(e) => setServico(a => ({ ...a, liberacao_averbacao_execucao: e.target.checked }))}
                                        />}
                                    label='Liberação de Averbação de Execução'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.liberacao_restricao_financeira}
                                            onChange={(e) => setServico(a => ({ ...a, liberacao_restricao_financeira: e.target.checked }))}
                                        />}
                                    label='Liberação de Restrição Financeira'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.licenca_especial_transito}
                                            onChange={(e) => setServico(a => ({ ...a, licenca_especial_transito: e.target.checked }))}
                                        />}
                                    label='Licença especial de Trânsito'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.mudanca_placa_unica}
                                            onChange={(e) => setServico(a => ({ ...a, mudanca_placa_unica: e.target.checked }))}
                                        />}
                                    label='Mudança Para Placa Única'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.primeiro_emplamaneto}
                                            onChange={(e) => setServico(a => ({ ...a, primeiro_emplamaneto: e.target.checked }))}
                                        />}
                                    label='Primeiro Emplacamento'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.renovacao_placa_experiencia_fabricante}
                                            onChange={(e) => setServico(a => ({ ...a, renovacao_placa_experiencia_fabricante: e.target.checked }))}
                                        />}
                                    label='Renovação de Placa de Experiência ou de Fabricante'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.reserva_placa}
                                            onChange={(e) => setServico(a => ({ ...a, reserva_placa: e.target.checked }))}
                                        />}
                                    label='Reserva de Placa'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.restricao_transferencia}
                                            onChange={(e) => setServico(a => ({ ...a, restricao_transferencia: e.target.checked }))}
                                        />}
                                    label='Restrição por Transferência'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.solicitacao_autorizacao_alteracao_caracteristicas}
                                            onChange={(e) => setServico(a => ({ ...a, solicitacao_autorizacao_alteracao_caracteristicas: e.target.checked }))}
                                        />}
                                    label='Solicitação de Autorização para Alteração de Características'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.solicitacao_autorizacao_regravacao_chassi}
                                            onChange={(e) => setServico(a => ({ ...a, solicitacao_autorizacao_regravacao_chassi: e.target.checked }))}
                                        />}
                                    label='Solicitação de Autorização para Regravação de Nº de Chassi'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={servico.solicitacao_autorizacao_transporte_escolar}
                                            onChange={(e) => setServico(a => ({ ...a, solicitacao_autorizacao_transporte_escolar: e.target.checked }))}
                                        />}
                                    label='Solicitação de Autorização para Transporte Escolar'
                                />
                            </div>
                        </div>
                        <div className={style.boxlinha}>
                            <h2>Novo Endereço: Rua/Av.:</h2>
                            <FormControl
                                disabled={!servico.alteracao_endereco_entrega_CRL_CRLV}
                                fullWidth variant="standard" >
                                <Input
                                    required
                                    value={novoEndereco.novo_endereco}
                                    onChange={(e) => setNovoEndereco(a => ({ ...a, novo_endereco: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>Nº:</h2>
                            <FormControl
                                disabled={!servico.alteracao_endereco_entrega_CRL_CRLV}
                                sx={{ width: "20%" }} variant="standard" >
                                <Input
                                    required
                                    value={novoEndereco.numero}
                                    onChange={(e) => setNovoEndereco(a => ({ ...a, numero: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                        </div>
                        <div className={style.boxlinha}>
                            <h2>Bairro:</h2>
                            <FormControl
                                disabled={!servico.alteracao_endereco_entrega_CRL_CRLV}
                                sx={{ width: "50%" }} variant="standard" >
                                <Input
                                    required
                                    value={novoEndereco.bairro}
                                    onChange={(e) => setNovoEndereco(a => ({ ...a, bairro: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>Cidade:</h2>
                            <FormControl
                                disabled={!servico.alteracao_endereco_entrega_CRL_CRLV}
                                fullWidth variant="standard" >
                                <Input
                                    required
                                    value={novoEndereco.cidade}
                                    onChange={(e) => setNovoEndereco(a => ({ ...a, cidade: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>UF:</h2>
                            <FormControl
                                disabled={!servico.alteracao_endereco_entrega_CRL_CRLV}
                                sx={{ width: "15%" }} variant="standard" >
                                <Input
                                    required
                                    value={novoEndereco.uf}
                                    onChange={(e) => setNovoEndereco(a => ({ ...a, uf: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>CEP:</h2>
                            <FormControl
                                disabled={!servico.alteracao_endereco_entrega_CRL_CRLV}
                                sx={{ width: "30%" }} variant="standard" >
                                <Input
                                    required
                                    value={novoEndereco.cep}
                                    onChange={(e) => setNovoEndereco(a => ({ ...a, cep: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                        </div>
                        <h2 className={style.linhaservico}> </h2>
                        <div className={style.container}>
                            <div className={style.boxrigth}>
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.averbacao_execucao} onChange={(e) => setServico2(a => ({ ...a, averbacao_execucao: e.target.checked }))} />} label='Averbação de Execução' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.baixa_placa_experiencia_fabricante} onChange={(e) => setServico2(a => ({ ...a, baixa_placa_experiencia_fabricante: e.target.checked }))} />} label='Baixa de Placa de Experiência ou de Fabricante' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.baixa_outra_uf} onChange={(e) => setServico2(a => ({ ...a, baixa_outra_uf: e.target.checked }))} />} label='Baixa para outra UF' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.baixa_veiculo} onChange={(e) => setServico2(a => ({ ...a, baixa_veiculo: e.target.checked }))} />} label='Baixa de Veículo' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.cancelamento_processo} onChange={(e) => setServico2(a => ({ ...a, cancelamento_processo: e.target.checked }))} />} label='Cancelamento de Processo' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.colocacao_lacre_placa} onChange={(e) => setServico2(a => ({ ...a, colocacao_lacre_placa: e.target.checked }))} />} label='Colocação em Lacre de Placa' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.comunicacao_venda} onChange={(e) => setServico2(a => ({ ...a, comunicacao_venda: e.target.checked }))} />} label='Comunicação de Venda' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.correcao_observacao_CRV_CRLV} onChange={(e) => setServico2(a => ({ ...a, correcao_observacao_CRV_CRLV: e.target.checked }))} />} label='Correção das Observações do CRV/CRLV' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.correcao_chassi} onChange={(e) => setServico2(a => ({ ...a, correcao_chassi: e.target.checked }))} />} label='Correção de Chassi' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.correcao_info_proprietario} onChange={(e) => setServico2(a => ({ ...a, correcao_info_veiculo: e.target.checked }))} />} label='Correção de Informações do Proprietário' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.correcao_info_veiculo} onChange={(e) => setServico2(a => ({ ...a, correcao_info_proprietario: e.target.checked }))} />} label='Correção de Informações do Veículo' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.correcao_municipio} onChange={(e) => setServico2(a => ({ ...a, correcao_municipio: e.target.checked }))} />} label='Correção de Município' />
                                <FormControlLabel sx={{ height: 22, mt: 2, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.autorizacao_detran} onChange={(e) => setServico2(a => ({ ...a, autorizacao_detran: e.target.checked }))} />} label='Autorizo o DETRAN/RS a enviar por email ou telefone' />
                                <h2 className={style.nocheck}>celular as informações do meu interesse junto a este órgão.</h2>
                                <FormControlLabel sx={{ height: 22, mt: 2, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.nao_autorizacao_detran} onChange={(e) => setServico2(a => ({ ...a, nao_autorizacao_detran: e.target.checked }))} />} label='Não autorizo o recebimento de informações do DETRAN/RS' />
                                <h2 className={style.nocheck}>e estou ciente de que será minha responsabilidade obter as</h2>
                                <h2 className={style.nocheck}>informações relevantes ao meu veículo</h2>
                            </div>
                            <div className={style.boxrigth}>
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.solicitacao_certidao} onChange={(e) => setServico2(a => ({ ...a, solicitacao_certidao: e.target.checked }))} />} label='Solicitação de Certidão' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.solicitacao_copia_documento} onChange={(e) => setServico2(a => ({ ...a, solicitacao_copia_documento: e.target.checked }))} />} label='Solicitação de Cópia de Documentos' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.solicitacao_copia_CRLV} onChange={(e) => setServico2(a => ({ ...a, solicitacao_copia_CRLV: e.target.checked }))} />} label='Solicitação de Cópia de CRLV' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.solicitacao_vistoria} onChange={(e) => setServico2(a => ({ ...a, solicitacao_vistoria: e.target.checked }))} />} label='Solicitação de Vistoria' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.solicitacao_DCPPO} onChange={(e) => setServico2(a => ({ ...a, solicitacao_DCPPO: e.target.checked }))} />} label='Solicitação de DCPPO' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.transferencia_propriedade_veiculo_outro_uf} onChange={(e) => setServico2(a => ({ ...a, transferencia_propriedade_veiculo_outro_uf: e.target.checked }))} />} label='Transferência de Propriedade de Veículo de Outro Estado' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.transferencia_propriedade_veiculo} onChange={(e) => setServico2(a => ({ ...a, transferencia_propriedade_veiculo: e.target.checked }))} />} label='Transferência de Propriedade de Veículo do RS' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.troca_municipio_veiculo_outro_uf} onChange={(e) => setServico2(a => ({ ...a, troca_municipio_veiculo_outro_uf: e.target.checked }))} />} label='Troca de Município Veículo de Outro Estado' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.troca_municipio_veiculo} onChange={(e) => setServico2(a => ({ ...a, troca_municipio_veiculo: e.target.checked }))} />} label='Troca de Município Veículo do RS' />
                                <FormControlLabel sx={{ height: 22, whiteSpace: "nowrap" }} control={<Checkbox size='small' checked={servico2.outros} onChange={(e) => setServico2(a => ({ ...a, outros: e.target.checked, outros_entradas: "" }))} />} label='Outros:' />
                                <FormControl
                                    sx={{ minWidth: '150px' }}
                                    disabled={!servico2.outros}
                                    variant="standard" >
                                    <Input
                                        required
                                        value={servico2.outros_entradas}
                                        onChange={(e) => setServico2(a => ({ ...a, outros_entradas: e.target.value }))}
                                        startAdornment={<InputAdornment position="start"></InputAdornment>}
                                    />
                                </FormControl>
                                <h2 className={style.linhaservico}> </h2>
                                <div className={style.boxlinha}>
                                    <h2>Relativo ao Veículo:</h2>
                                    <FormControl
                                        fullWidth
                                        variant="standard" >
                                        <Input
                                            value={veic.ao_veiculo}
                                            onChange={(e) => setVeic(a => ({ ...a, ao_veiculo: e.target.value }))}
                                            startAdornment={<InputAdornment position="start"></InputAdornment>}
                                        />
                                    </FormControl>
                                </div>
                                <div className={style.boxlinha}>
                                    <h2>Chassi/Placa:</h2>
                                    <FormControl
                                        fullWidth
                                        variant="standard" >
                                        <Input
                                            value={veic.chassi_ou_placa}
                                            onChange={(e) => setVeic(a => ({ ...a, chassi_ou_placa: e.target.value }))}
                                            startAdornment={<InputAdornment position="start"></InputAdornment>}
                                        />
                                    </FormControl>
                                </div>
                            </div>
                        </div>
                        <div className={style.centrador}>
                            <input className={style.data} type='date' value={d} onChange={e => setD(e.target.value)} />
                            <div className={style.boxlinha}>
                                <Button sx={{ mt: 5 }} color='success' type='submit' >Salvar</Button>
                            </div>
                        </div>
                    </Paper>
                </Box>
            </form>
        </>
    )
}