import * as React from 'react';
import style from './Servico.module.scss'
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { Identificador } from './Passo1/Identificador';
import { CadastroVeicular } from './Passo2/CadastroVeicular';
import { SolicitaServico } from './Passo4/SolicitaServico';
import { UploadArquivo } from './Passo5/UploadArquivo';
import { ValidaGRT1, ValidaGRT2 } from './Passo6/ValidaGRT';
import { CadastroCliente } from './Passo3/CadastroCliente';
import { ImprimirGRT } from './Passo7/ImprimirGRT';

const steps = ['Identificação do Veículo', 'Cadastro do Veículo', 'Cadastro do Cliente', 'Solicitação de Serviço', 'Coleta dos Documentos', 'Validação do GRT Frente', 'Revisão do Dados'];

export const Service = ({ loginUser, setActiveStep, activeStep, veiculo, setVeiculo }) => {

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setCadastroVeiculo(false)
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
    setCadastroServico(false)
    setCadastroServico(true)
  };

  const handleReset = () => {
    setActiveStep(0);
  };
  
  const [placa, setPlaca] = React.useState('');
  const [cadastroVeiculo, setCadastroVeiculo] = React.useState(false)
  const [cadastroCliente, setCadastroCliente] = React.useState(false)
  const [cadastroServico, setCadastroServico] = React.useState(false)
  const [coletaDocumento, setColetaDocumento] = React.useState(false)
  const [Imprimegrt1, setImprimegrt1] = React.useState(false)
  const [Imprimegrt2, setImprimegrt2] = React.useState(false)
  const [revisaDoc, setRevisaDoc] = React.useState(false)

  var valida = false

  switch (activeStep) {
    case 0:
      placa.length === 7 ? valida = false : valida = true;
      break;
    case 1:
      cadastroVeiculo ? valida = false : valida = true;
      break;
    case 2:
      cadastroCliente ? valida = false : valida = true;
      break;
    case 3:
      cadastroServico ? valida = false : valida = true;
      break;
    case 4:
      coletaDocumento ? valida = false : valida = true;
      break;
    case 5:
      Imprimegrt1 ? valida = false : valida = true;
      break;
    case 6:
      Imprimegrt2 ? valida = false : valida = true;
      break;
    case 7:
      revisaDoc ? valida = false : valida = true;
      break;
    // no default
  }

  return (
    <Box sx={{ width: '100%' }}>
      <Stepper activeStep={activeStep} className={style.stepper} >
        {steps.map((label) => {
          const stepProps = {};
          const labelProps = {};
          return (
            <Step key={label} {...stepProps}>
              <StepLabel {...labelProps}>{label}</StepLabel>
            </Step>
          );
        })}
      </Stepper>
      {activeStep === steps.length ? (
        <React.Fragment>
          <Typography sx={{ mt: 2, mb: 1 }}>
            Serviço solicitado com sucesso
          </Typography>
          <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
            <Box sx={{ flex: '1 1 auto' }} />
            <Button onClick={handleReset}>Resetar</Button>
          </Box>
        </React.Fragment>
      ) : (
        <React.Fragment>
          <Typography sx={{ mt: 2, mb: 1 }} className={style.stepper} >Etapa {activeStep + 1}</Typography>
          <Box sx={{ display: 'flex', flexDirection: 'row', flexWrap: 'wrap', justifyContent: 'space-around' }} >
            {activeStep === 0 && <Identificador placa={placa} setPlaca={setPlaca} />}
            {activeStep === 1 && <CadastroVeicular veiculo={veiculo} setVeiculo={setVeiculo} loginUser={loginUser} placa={placa} setCadastroVeiculo={setCadastroVeiculo} />}
            {activeStep === 2 && <CadastroCliente veiculo={veiculo} setCadastroCliente={setCadastroCliente} /> }
            {activeStep === 3 && <SolicitaServico veiculo={veiculo} setCadastroServico={setCadastroServico} user={loginUser} /> }
            {activeStep === 4 && <UploadArquivo user={loginUser} veiculo={veiculo} setColetaDocumento={setColetaDocumento}/> }
            {activeStep === 5 && <ValidaGRT1 user={loginUser} veiculo={veiculo} setImprimegrt1={setImprimegrt1} /> }
            {activeStep === 6 && <ValidaGRT2 user={loginUser} veiculo={veiculo} setImprimegrt2={setImprimegrt2} /> }
            {activeStep === 6 && <ImprimirGRT setRevisaDoc={setRevisaDoc} /> }
          </Box>
          <Box className={style.stepper} sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
            <Button
              color="inherit"
              disabled={activeStep === 0}
              onClick={handleBack}
              sx={{ mr: 1 }}
            >
              Voltar
            </Button>
            <Box sx={{ flex: '1 1 auto' }} />

            <Button id={steps[activeStep]} disabled={valida} color='success' onClick={handleNext}>
              {activeStep === steps.length - 1 ? 'Finalizar' : 'Próximo'}
            </Button>
          </Box>
        </React.Fragment>
      )}
    </Box>
  );
}