import { Alert, Button, Checkbox, FormControl, FormControlLabel, Input, InputAdornment, Paper, Snackbar, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { useState } from "react";
import style from './CadastroCliente.module.scss';
import moment from "moment";
import axios from "axios";

export const CadastroCliente = ({ veiculo, setCadastroCliente }) => {
    // console.log(veiculo)

    const [cadastro, setCadastro] = useState({
        nome: '',
        rgOuCnpj: '',
        endereco: '',
        utilizaEndereco: false,
        celular: '',
        email: '',
        restricaoFinanceira: false,
        agenteFinanceiro: '',
        alteraEndereco: false,
        novoEndereco: '',
        novoenderecoCEP: '',
        alteraInfoProprietario: false,
        alteraInfoVeiculo: false,
        alteraInfoFinanceira: false,
        alteraInfoEnderecoEntrega: false,
        novoEnderecoLogra: '',
        novoEnderecoNum: '',
        novoEnderecoBairro: '',
        novoEnderecoCidade: '',
        novoEnderecoUF: '',
        novoEnderecoCEP: '',
        correcaoRestricoes: false,
        emissaoDCPPO: false,
        fornecimentoPlacaExperienciaFabricante: false,
        inclusaoRestricaoFinanceria: false,
        liberacaoAverbacaoExecucao: false,
        liberacaoRestricaoFinanceira: false,
        licencaEspecialTransito: false,
        mudancaPlacaUnica: false,
        primeiroEmplacamento: false,
        renovacaoPlacaExperienciaFabricante: false,
        reservaPlaca: false,
        restricaoTransferencia: false,
        solicitacaoAlteracaoCaracteristicas: false,
        solicitacaoRegravacaoChassi: false,
        solicitacaoTransporteEscolar: false,
        alteracaoExecucao: false,
        baixaPlacaExperienciaFabricante: false,
        baixaOutraUF: false,
        baixaVeiculo: false,
        baixaVeiculoCampo: '',
        cancelamentoProcesso: false,
        colocacaoLacrePlaca: false,
        comunicacaoVenda: false,
        correcaoObservacoes: false,
        correcaoChassi: false,
        correcaoInformacoesProprietario: false,
        correcaoInformacoesVeiculo: false,
        correcaoMunicipio: false,
        autorizoEmailTelefone: false,
        naoAutorizoInformacoes: false,
        solicitaCertidao: false,
        solicitaCopiaDocumento: false,
        solicitaCopiaCRLV: false,
        solicitaVistoria: false,
        solicitaDCPPO: false,
        transferePropriedadeVeiculoOutro: false,
        transferePropriedadeVeiculoRS: false,
        trocaMunicipioOutroEstado: false,
        trocaMunicipioRS: false,
        Outros: false,
        campoOutros: '',
        relativoVeiculo: veiculo.data.modelo,
        relativoChassiOuPlaca: veiculo.data.chassi,
        data: moment().format("YYYY-MM-DD"),
    })

    const [segundaVia, setSegundaVia] = useState({
        CRLV: false,
        CRV_CRLV: false,
        segundaViaMotivo: ''
    })

    const [email1, setEmail1] = useState(false)

    const CadastraCelular = (e) => {
        const numero = e.target.value.replace(/[^0-9]/g, '');
        // console.log(numero)
        setCadastro(a => ({ ...a, celular: numero }))
    }

    const CadastroEmail = (e) => {
        let arroba = cadastro.email.includes("@")
        let ponto = cadastro.email.includes('.')
        if ((arroba === false) || (ponto === false)) {
            setEmail1(true)
        } else {
            setEmail1(false)
        }
    }

    const registroFinanceiro = (e) => {
        setCadastro(a => ({ ...a, restricaoFinanceira: false, agenteFinanceiro: '' }))
    }

    const SegundaVia = (e) => {
        const valida = e.target.id;
        // console.log(e.target.id)
        // console.log(e.target.checked)
        valida === 'CRLV' && setSegundaVia(a => ({ ...a, CRLV: e.target.checked, CRV_CRLV: false, segundaViaMotivo: '' }))
        valida === 'CRV_CRLV' && setSegundaVia(a => ({ ...a, CRLV: false, CRV_CRLV: e.target.checked, segundaViaMotivo: '' }))
    }

    const CadastroCliente = (e) => {
        e.preventDefault()
        console.log("Tentativa de cadastro")
        console.log(cadastro)
        axios.post('http://104.248.40.57:3001/Servicos/CadastrarCliente', {
            cadastro
        })
            .then(res => {
                setCadastroCliente(true)
                avisoPositivo()
                // console.log('Cliente cadastrado', res)
            })
            .catch(err => {
                avisoNegativo()
                // console.log('Cliente não cadastrado', err)
            })
    }

    const [ op, setOp ] = useState({
        op: false,
        tipo: 'info',
        frase: ''
    })
    const fecharAviso = () => { setOp(a => ({...a, op: false})) }
    const avisoPositivo = () => {
        setOp(a => ({...a,
            op: true,
            tipo: 'success',
            frase: 'Autorização do cliente cadastrada com sucesso!'
        }))
    }
    const avisoNegativo = () => {
        setOp(a => ({...a, 
            op: true,
            tipo: 'error',
            frase: 'Autorização do cliente não cadastrada, tente novamente!'
        }))
    }

    return (
        <>
        <Snackbar
            open={op.op}
            autoHideDuration={6000}
            onClose={fecharAviso}
        >
            <Alert onClose={fecharAviso} severity={op.tipo} sx={{ width: '100%'}} >
                {op.frase}
            </Alert>
        </Snackbar>
            <form className={style.opcoes} onSubmit={CadastroCliente}>
                <Box sx={{ width: '100%' }}>
                    <Paper sx={{ p: 2 }}>
                        <Typography align="center" variant="h5">AUTORIZAÇÃO DO CLIENTE</Typography>
                        <div className={style.boxlinha}>
                            <h2>Eu,</h2>
                            <FormControl fullWidth variant="standard">
                                <Input
                                    value={cadastro.nome}
                                    onChange={(e) => setCadastro(a => ({ ...a, nome: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>RG / CNPJ</h2>
                            <FormControl variant="standard" sx={{ width: '22%' }}>
                                <Input
                                    value={cadastro.rgOuCnpj}
                                    onChange={(e) => setCadastro(a => ({ ...a, rgOuCnpj: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                        </div>
                        <div className={style.boxlinha}>
                            <h2>Endereço:</h2>
                            <FormControl variant="standard" fullWidth>
                                <Input
                                    value={cadastro.endereco}
                                    onChange={(e) => setCadastro(a => ({ ...a, endereco: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                        </div>
                        <div className={style.linha}>
                            <FormControlLabel control={
                                <Checkbox
                                    checked={cadastro.utilizaEndereco}
                                    onChange={(e) => setCadastro(a => ({ ...a, utilizaEndereco: e.target.checked }))}
                                />}
                                label='Utilizar o endereço da CNH emitida no Rio Grande do Sul.'
                            />
                        </div>
                        <div className={style.boxlinha}>
                            <h2>Celular:</h2>
                            <FormControl sx={{ width: '45%' }} variant="standard">
                                <Input
                                    id="celular"
                                    value={cadastro.celular}
                                    onChange={CadastraCelular}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>e/ou E-mail:</h2>
                            <FormControl variant="standard" fullWidth >
                                <Input
                                    id="email"
                                    value={cadastro.email}
                                    onChange={(e) => setCadastro(a => ({ ...a, email: e.target.value }))}
                                    onBlur={CadastroEmail}
                                    error={email1}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>para recebimento de informações</h2>
                        </div>
                        <div className={style.linhaservico}>
                            <h2 className={style.umalinha}>relevanes ao veículo de sua propriedade registrado no DETRAN/RG</h2>
                            <h2 className={style.umalinha}>Proprietário do veículo abaixo descrito, descrito, autorizo o Despachante / Preposto identificado no anverso a solicitar os seguintes</h2>
                            <h2 className={style.umalinha}>serviços em meu nome:</h2>
                        </div>
                        <div className={style.container}>
                            <div className={style.boxleft}>
                                <h2>Haverá inclusão ou alteração de restrição financeira?</h2>
                                <div className={style.unicoassim}>
                                    <div>
                                        <FormControlLabel control={
                                            <Checkbox
                                                checked={cadastro.restricaoFinanceira}
                                                onChange={(e) => setCadastro(a => ({ ...a, restricaoFinanceira: true }))} />}
                                            label='Sim'
                                        />
                                    </div>
                                    <div>
                                        <FormControlLabel control={
                                            <Checkbox
                                                checked={!cadastro.restricaoFinanceira}
                                                onChange={registroFinanceiro} />}
                                            label="Não"
                                        />
                                    </div>
                                </div>
                                <div className={style.boxlinha}>
                                    <h2>Agente Financeiro:</h2>
                                    <FormControl disabled={!cadastro.restricaoFinanceira} fullWidth variant="standard">
                                        <Input
                                            id="standard-adornment-amount"
                                            value={cadastro.agenteFinanceiro}
                                            onChange={(e) => setCadastro(a => ({ ...a, agenteFinanceiro: e.target.value }))}
                                            startAdornment={<InputAdornment position="start"></InputAdornment>}
                                        />
                                    </FormControl>
                                </div>
                                <div className={style.boxlinha}>
                                    <FormControlLabel control={<Checkbox id='CRLV' checked={segundaVia.CRLV} onChange={SegundaVia} />} label='2ª Via CRLV' />
                                    <FormControlLabel control={<Checkbox id='CRV_CRLV' checked={segundaVia.CRV_CRLV} onChange={SegundaVia} />} label='2ª Via CRV/CRLV' />
                                </div>
                                <div className={style.boxlinha}>
                                    <h2>Motivo:</h2>
                                    <FormControl
                                        disabled={segundaVia.CRLV === false ? segundaVia.CRV_CRLV === false ? true : false : false}
                                        fullWidth variant="standard">
                                        <Input
                                            id="standard-adornment-amount"
                                            value={segundaVia.segundaViaMotivo}
                                            onChange={(e) => setSegundaVia(a => ({ ...a, segundaViaMotivo: e.target.value }))}
                                            startAdornment={<InputAdornment position="start"></InputAdornment>}
                                        />
                                    </FormControl>
                                </div>
                                <div className={style.boxlinha}>
                                    <FormControlLabel control={
                                        <Checkbox
                                            checked={cadastro.alteraEndereco}
                                            onChange={(e) => setCadastro(a => ({
                                                ...a,
                                                alteraEndereco: e.target.checked,
                                                novoEndereco: e.target.checked === true ? "" : "",
                                                novoenderecoCEP: e.target.checked === true ? "" : ""
                                            }))} />}
                                        label="Alteração do endereço residencial"
                                    />
                                </div>
                                <div className={style.boxlinha}>
                                    <h2>Novo endereço:</h2>
                                    <FormControl
                                        disabled={!cadastro.alteraEndereco}
                                        fullWidth variant="standard">
                                        <Input
                                            id="standard-adornment-amount"
                                            value={cadastro.novoEndereco}
                                            onChange={(e) => setCadastro(a => ({ ...a, novoEndereco: e.target.value }))}
                                            startAdornment={<InputAdornment position="start"></InputAdornment>}
                                        />
                                    </FormControl>
                                </div>
                                <div className={style.boxlinha}>
                                    <Box sx={{ mb: 2, display: 'flex', alignItems: 'center', width: '100%' }}>
                                        <h2>CEP:</h2>
                                        <FormControl
                                            disabled={!cadastro.alteraEndereco}
                                            fullWidth variant="standard">
                                            <Input
                                                id="standard-adornment-amount"
                                                value={cadastro.novoenderecoCEP}
                                                onChange={(e) => setCadastro(a => ({ ...a, novoenderecoCEP: e.target.value }))}
                                                startAdornment={<InputAdornment position="start"></InputAdornment>}
                                            />
                                        </FormControl>
                                    </Box>
                                </div>
                                <div className={style.coluna}>
                                    <FormControlLabel
                                        sx={{ height: 22 }}
                                        control={
                                            <Checkbox
                                                size='small'
                                                checked={cadastro.alteraInfoProprietario}
                                                onChange={(e) => setCadastro(a => ({ ...a, alteraInfoProprietario: e.target.checked }))}
                                            />}
                                        label='Alteração de Informações do Proprietário.'
                                    />
                                    <FormControlLabel
                                        sx={{ height: 22 }}
                                        control={
                                            <Checkbox
                                                size='small'
                                                checked={cadastro.alteraInfoVeiculo}
                                                onChange={(e) => setCadastro(a => ({ ...a, alteraInfoVeiculo: e.target.checked }))}
                                            />}
                                        label='Alteração de Informações do Veículo.'
                                    />
                                    <FormControlLabel
                                        sx={{ height: 22 }}
                                        control={
                                            <Checkbox
                                                size='small'
                                                checked={cadastro.alteraInfoFinanceira}
                                                onChange={(e) => setCadastro(a => ({ ...a, alteraInfoFinanceira: e.target.checked }))}
                                            />}
                                        label='Alteração de Restrição Financeira.'
                                    />
                                    <FormControlLabel
                                        sx={{ height: 22 }}
                                        control={
                                            <Checkbox
                                                size='small'
                                                checked={cadastro.alteraInfoEnderecoEntrega}
                                                onChange={(e) => setCadastro(a => ({
                                                    ...a,
                                                    alteraInfoEnderecoEntrega: e.target.checked,
                                                    novoEnderecoLogra: "",
                                                    novoEnderecoNum: "",
                                                    novoEnderecoBairro: "",
                                                    novoEnderecoCidade: "",
                                                    novoEnderecoUF: "",
                                                    novoEnderecoCEP: "",
                                                }))}
                                            />}
                                        label='Alteração de Endereço de entrega do CRV / CRLV.'
                                    />
                                </div>
                            </div>
                            <div className={style.boxrigth}>
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.correcao_restricao}
                                            onChange={(e) => setCadastro(a => ({ ...a, emissaoDCPPO: e.target.checked }))}
                                        />}
                                    label='Correção de Restrições'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.fornecimentoPlacaExperienciaFabricante}
                                            onChange={(e) => setCadastro(a => ({ ...a, fornecimentoPlacaExperienciaFabricante: e.target.checked }))}
                                        />}
                                    label='Fornecimento de Placa de Experiencia ou de Fabricante'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.inclusaoRestricaoFinanceria}
                                            onChange={(e) => setCadastro(a => ({ ...a, inclusaoRestricaoFinanceria: e.target.checked }))}
                                        />}
                                    label='Inclusão de Restrição Financeira'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.liberacaoAverbacaoExecucao}
                                            onChange={(e) => setCadastro(a => ({ ...a, liberacaoAverbacaoExecucao: e.target.checked }))}
                                        />}
                                    label='Liberação de Averbação de Execução'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.liberacaoRestricaoFinanceira}
                                            onChange={(e) => setCadastro(a => ({ ...a, liberacaoRestricaoFinanceira: e.target.checked }))}
                                        />}
                                    label='Liberação de Restrição Financeira'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.licencaEspecialTransito}
                                            onChange={(e) => setCadastro(a => ({ ...a, licencaEspecialTransito: e.target.checked }))}
                                        />}
                                    label='Licença especial de Trânsito'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.mudancaPlacaUnica}
                                            onChange={(e) => setCadastro(a => ({ ...a, mudancaPlacaUnica: e.target.checked }))}
                                        />}
                                    label='Mudança para Placa Única'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.primeiroEmplacamento}
                                            onChange={(e) => setCadastro(a => ({ ...a, primeiroEmplacamento: e.target.checked }))}
                                        />}
                                    label='Primeiro Emplacamento'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.renovacaoPlacaExperienciaFabricante}
                                            onChange={(e) => setCadastro(a => ({ ...a, renovacaoPlacaExperienciaFabricante: e.target.checked }))}
                                        />}
                                    label='renovação de Placa de Experência ou de Fabricante'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.reservaPlaca}
                                            onChange={(e) => setCadastro(a => ({ ...a, reservaPlaca: e.target.checked }))}
                                        />}
                                    label='Reserva de Placa'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.restricaoTransferencia}
                                            onChange={(e) => setCadastro(a => ({ ...a, restricaoTransferencia: e.target.checked }))}
                                        />}
                                    label='Restrição por Transferência'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.solicitacaoAlteracaoCaracteristicas}
                                            onChange={(e) => setCadastro(a => ({ ...a, solicitacaoAlteracaoCaracteristicas: e.target.checked }))}
                                        />}
                                    label='Solicitação de Autorização para Alteração de Caracteristicas'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.solicitacaoRegravacaoChassi}
                                            onChange={(e) => setCadastro(a => ({ ...a, solicitacaoRegravacaoChassi: e.target.checked }))}
                                        />}
                                    label='Solicitação de Autorização para Regravação de Nº de Chassi'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.solicitacaoTransporteEscolar}
                                            onChange={(e) => setCadastro(a => ({ ...a, solicitacaoTransporteEscolar: e.target.checked }))}
                                        />}
                                    label='Soliciatação de Autorização para Transporte Escolar'
                                />
                            </div>
                        </div>
                        <div className={style.boxlinha}>
                            <h2>Novo Endereço: Rua/AV:</h2>
                            <FormControl fullWidth variant="standard">
                                <Input
                                    id="standard-adornment-amount"
                                    disabled={!cadastro.alteraInfoEnderecoEntrega}
                                    value={cadastro.novoEnderecoLogra}
                                    onChange={(e) => setCadastro(a => ({ ...a, novoEnderecoLogra: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>Nº</h2>
                            <FormControl sx={{ width: "35%" }} variant="standard">
                                <Input
                                    id="standard-adornment-amount"
                                    disabled={!cadastro.alteraInfoEnderecoEntrega}
                                    value={cadastro.novoEnderecoNum}
                                    onChange={(e) => setCadastro(a => ({ ...a, novoEnderecoNum: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                        </div>
                        <div className={style.boxlinha}>
                            <h2>Bairro:</h2>
                            <FormControl sx={{ width: "50%" }} variant="standard">
                                <Input
                                    id="standard-adornment-amount"
                                    disabled={!cadastro.alteraInfoEnderecoEntrega}
                                    value={cadastro.novoEnderecoBairro}
                                    onChange={(e) => setCadastro(a => ({ ...a, novoEnderecoBairro: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>Cidade:</h2>
                            <FormControl fullWidth variant="standard">
                                <Input
                                    id="standard-adornment-amount"
                                    disabled={!cadastro.alteraInfoEnderecoEntrega}
                                    value={cadastro.novoEnderecoCidade}
                                    onChange={(e) => setCadastro(a => ({ ...a, novoEnderecoCidade: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>UF:</h2>
                            <FormControl sx={{ width: "10%" }} variant="standard">
                                <Input
                                    id="standard-adornment-amount"
                                    disabled={!cadastro.alteraInfoEnderecoEntrega}
                                    value={cadastro.novoEnderecoUF}
                                    onChange={(e) => setCadastro(a => ({ ...a, novoEnderecoUF: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>CEP:</h2>
                            <FormControl sx={{ width: "30%" }} variant="standard">
                                <Input
                                    id="standard-adornment-amount"
                                    disabled={!cadastro.alteraInfoEnderecoEntrega}
                                    value={cadastro.novoEnderecoCEP}
                                    onChange={(e) => setCadastro(a => ({ ...a, novoEnderecoCEP: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                        </div>
                        <h2 className={style.linhaservico}> </h2>
                        <div className={style.container}>
                            <div className={style.boxrigth}>
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.alteracaoExecucao}
                                            onChange={(e) => setCadastro(a => ({ ...a, alteracaoExecucao: e.target.checked }))}
                                        />}
                                    label='Alteração de Execução'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.baixaPlacaExperienciaFabricante}
                                            onChange={(e) => setCadastro(a => ({ ...a, baixaPlacaExperienciaFabricante: e.target.checked }))}
                                        />}
                                    label='Baixa de Placa de Experiência ou de Fabricante'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.baixaOutraUF}
                                            onChange={(e) => setCadastro(a => ({ ...a, baixaOutraUF: e.target.checked }))}
                                        />}
                                    label='Baixa para outra UF'
                                />
                                <div className={style.boxlinha}>
                                    <FormControlLabel
                                        sx={{ height: 22, whiteSpace: "nowrap" }}
                                        control={
                                            <Checkbox
                                                size='small'
                                                checked={cadastro.baixaVeiculo}
                                                onChange={(e) => setCadastro(a => ({ ...a, baixaVeiculo: e.target.checked, baixaVeiculoCampo: "" }))}
                                            />}
                                        label='Baixa de Veículo'
                                    />
                                    <FormControl
                                        fullWidth
                                        variant="standard" >
                                        <Input
                                            disabled={!cadastro.baixaVeiculo}
                                            value={cadastro.baixaVeiculoCampo}
                                            onChange={(e) => setCadastro(a => ({ ...a, baixaVeiculoCampo: e.target.value }))}
                                            startAdornment={<InputAdornment position="start"></InputAdornment>}
                                        />
                                    </FormControl>
                                </div>
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.cancelamentoProcesso}
                                            onChange={(e) => setCadastro(a => ({ ...a, cancelamentoProcesso: e.target.checked }))}
                                        />}
                                    label='Cancelamento de Processo'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.colocacaoLacrePlaca}
                                            onChange={(e) => setCadastro(a => ({ ...a, colocacaoLacrePlaca: e.target.checked }))}
                                        />}
                                    label='Colocação em Lacre de Placa'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.comunicacaoVenda}
                                            onChange={(e) => setCadastro(a => ({ ...a, comunicacaoVenda: e.target.checked }))}
                                        />}
                                    label='Comunicação de Venda'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.correcaoObservacoes}
                                            onChange={(e) => setCadastro(a => ({ ...a, correcaoObservacoes: e.target.checked }))}
                                        />}
                                    label='Correção das Observações do CRV / CRLV'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.correcaoChassi}
                                            onChange={(e) => setCadastro(a => ({ ...a, correcaoChassi: e.target.checked }))}
                                        />}
                                    label='Correção do Chassi'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.correcaoInformacoesProprietario}
                                            onChange={(e) => setCadastro(a => ({ ...a, correcaoInformacoesProprietario: e.target.checked }))}
                                        />}
                                    label='Correção de Informações do Proprietário'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.correcaoInformacoesVeiculo}
                                            onChange={(e) => setCadastro(a => ({ ...a, correcaoInformacoesVeiculo: e.target.checked }))}
                                        />}
                                    label='Correção de Informações de Veículo'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.correcaoMunicipio}
                                            onChange={(e) => setCadastro(a => ({ ...a, correcaoMunicipio: e.target.checked }))}
                                        />}
                                    label='Correção de Município'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, mt: 2, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.autorizoEmailTelefone}
                                            onChange={(e) => setCadastro(a => ({ ...a, autorizoEmailTelefone: e.target.checked }))}
                                        />}
                                    label='Autorizo o DETRAN/RS a enviar por email ou telefone celular'
                                />
                                <h2 className={style.nocheck} >as informações do meu interesse junto a este órgão.</h2>
                                <FormControlLabel
                                    sx={{ height: 22, mt: 2, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.naoAutorizoInformacoes}
                                            onChange={(e) => setCadastro(a => ({ ...a, naoAutorizoInformacoes: e.target.checked }))}
                                        />}
                                    label='Não autorizo o recebimento de informações do DETRAN/RS e'
                                />
                                <h2 className={style.nocheck}>estou ciente de que será minha responsabilidade obter</h2>
                                <h2 className={style.nocheck}>as informações relevantes ao meu veículo</h2>
                            </div>
                            <div className={style.boxrigth}>
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.solicitaCertidao}
                                            onChange={(e) => setCadastro(a => ({ ...a, solicitaCertidao: e.target.checked }))}
                                        />}
                                    label='Solicitação de Certidão'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.solicitaCopiaDocumento}
                                            onChange={(e) => setCadastro(a => ({ ...a, solicitaCopiaDocumento: e.target.checked }))}
                                        />}
                                    label='Solicitação de Cópia de Documentos'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.solicitaCopiaCRLV}
                                            onChange={(e) => setCadastro(a => ({ ...a, solicitaCopiaCRLV: e.target.checked }))}
                                        />}
                                    label='Solicitação de Cópia do CRLV'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.solicitaVistoria}
                                            onChange={(e) => setCadastro(a => ({ ...a, solicitaVistoria: e.target.checked }))}
                                        />}
                                    label='Solicitação de Vistoria'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.solicitaDCPPO}
                                            onChange={(e) => setCadastro(a => ({ ...a, solicitaDCPPO: e.target.checked }))}
                                        />}
                                    label='Solicitação de DCPPO'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.transferePropriedadeVeiculoOutro}
                                            onChange={(e) => setCadastro(a => ({ ...a, transferePropriedadeVeiculoOutro: e.target.checked }))}
                                        />}
                                    label='Transferência de Propriedade de Veículo de Outro Estado'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.transferePropriedadeVeiculoRS}
                                            onChange={(e) => setCadastro(a => ({ ...a, transferePropriedadeVeiculoRS: e.target.checked }))}
                                        />}
                                    label='Transferência de Propriedade de Veículo do RS'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.trocaMunicipioOutroEstado}
                                            onChange={(e) => setCadastro(a => ({ ...a, trocaMunicipioOutroEstado: e.target.checked }))}
                                        />}
                                    label='Troca de Município de Outro Estado'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.trocaMunicipioRS}
                                            onChange={(e) => setCadastro(a => ({ ...a, trocaMunicipioRS: e.target.checked }))}
                                        />}
                                    label='Troca de Município do RS'
                                />
                                <FormControlLabel
                                    sx={{ height: 22, whiteSpace: "nowrap" }}
                                    control={
                                        <Checkbox
                                            size='small'
                                            checked={cadastro.Outros}
                                            onChange={(e) => setCadastro(a => ({ ...a, Outros: e.target.checked, campoOutros: "" }))}
                                        />}
                                    label='Outros:'
                                />
                                <TextField
                                    disabled={!cadastro.Outros}
                                    variant='outlined'
                                    multiline
                                    rows={4}
                                    sx={{ m: '0 40px 0 0' }}
                                    value={cadastro.campoOutros}
                                    onChange={(e) => setCadastro(a => ({ ...a, campoOutros: e.target.value }))}
                                />
                            </div>
                        </div>
                        <div className={style.boxlinha}>
                            <h2>Relativo ao Veículo:</h2>
                            <FormControl fullWidth variant="standard">
                                <Input
                                    id="standard-adornment-amount"
                                    value={cadastro.relativoVeiculo}
                                    onChange={(e) => setCadastro(a => ({ ...a, relativoVeiculo: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                            <h2>Chassi / Placa:</h2>
                            <FormControl sx={{ width: '40%' }} variant="standard">
                                <Input
                                    id="standard-adornment-amount"
                                    value={cadastro.relativoChassiOuPlaca}
                                    onChange={(e) => setCadastro(a => ({ ...a, relativoChassiOuPlaca: e.target.value }))}
                                    startAdornment={<InputAdornment position="start"></InputAdornment>}
                                />
                            </FormControl>
                        </div>
                        <h2 className={style.linhaservico}> </h2>
                        <div className={style.centrador}>
                            <input className={style.data} type='date' value={cadastro.data} onChange={e => setCadastro(a => ({ ...a, data: e.target.value }))} />
                            <div className={style.boxlinha}>
                                <Button sx={{ mt: 5 }} color='success' type='submit' >Cadastrar</Button>
                            </div>
                        </div>
                    </Paper>
                </Box>
            </form>
        </>
    )
}