import './Cabecalho.module.scss'
import style from './Cabecalho.module.scss'
import crddrs from '../../img/crddrs.png'

export const Cabecalho = () => {


    return (
        <>
                <div className={style["title--bar"]}>
                    <img className={style["title--bar"]} alt='crddrs' src={crddrs} />
                    <h1>Conselho Regional dos Despachantes Documentalistas do Rio Grande do Sul</h1>
                </div>
                <div className={style['faixa--bar']}>
                    <div className={style.preto1}></div>
                    <div className={style.verde}></div>
                    <div className={style.vermelho}></div>
                    <div className={style.amarelo}></div>
                    <div className={style.preto2}></div>
                </div>
                <div className={style.space} ></div>
        </>
    )
}