import React from "react";
import { Box, Button, Collapse, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, IconButton, Table, TableBody, TableCell, TableHead, TableRow, Typography } from "@mui/material"

import EditIcon from '@mui/icons-material/Edit';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';

import style from './Linha.module.scss';

export const Linha = ({ row, setItens, setId, editUser, labelId, dense, DeleteUser, largura }) => {



    const [open, setOpen] = React.useState(false)
    const [dialog, setDialog] = React.useState(false)

    const DelUser = (e) => {
        setItens(e)
        setDialog(true)
    }

    function Editar(id) {
        setId(id);
        editUser();
    }

    // const back = {
    //     background: 'rgba(255, 0, 0, 0.2)',
    //     hover:   'rgba(255, 0, 0, 0.2)',      
    // }

    return (
        <>

            <Dialog
                open={dialog}
                onClose={() => setDialog(false)}
                aria-labelledby="apagar-cadastro-usuario"
                aria-describedby="Tela de confirmação para deletar um usuario definitivamente do sistema!"
            >
                <DialogTitle id='apagar-cadastro-usuario'>
                    {`Tem certeza que gostaria de apagar o usuario ${row.nome} ?`}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Ao confirmar a esclusão o usuario {row.nome} será apagado sendo impossível acessar suas informações ou cadastro.
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={DeleteUser} color='success'>Confirmar</Button>
                    <Button onClick={() => setDialog(false)} color='error' >Cancelar</Button>
                </DialogActions>
            </Dialog>
            <TableRow
                className={row.situacao === 'inativo' ? style.inativo : style.ativo}
                key={row._id}
            >
                {row.situacao === 'inativo' ? null : null}
            {row.situacao === 'inativo' ? null : null}
                {largura > 799 ?
                    <>
                        <TableCell sx={{ '& > *': { borderBottom: 'unset' } }}>
                            <div className={style.botoes}>
                                <button title="Editar" onClick={() => Editar(row)}>
                                    <EditIcon fontSize="small" color='success' />
                                </button>
                            </div>
                        </TableCell>
                        <TableCell
                            component="th"
                            id={labelId}
                            scope="row"
                            padding="none"
                        >
                            {row.nome}
                        </TableCell>
                        <TableCell align="right">{row.rg}</TableCell>
                        <TableCell align="right">{row.cpf}</TableCell>
                        {largura > 799 && <TableCell align="right">{row.tel}</TableCell>}
                        {largura > 799 && <TableCell align="right">{row.email}</TableCell>}
                        {largura > 799 && <TableCell align="right">{row.usuariotipo}</TableCell>}
                    </>
                    :
                    <>
                        <TableCell
                            component="th"
                            id={labelId}
                            scope="row"
                        >
                            {row.nome}
                        </TableCell>
                        <TableCell align="right">{row.cpf}</TableCell>
                    </>
                }



                <TableCell>
                    <IconButton
                        aria-label="expand row"
                        size="small"
                        onClick={() => setOpen(!open)}
                    >
                        {open ? <KeyboardArrowUpIcon color='primary' /> : <KeyboardArrowDownIcon color='primary' />}
                    </IconButton>
                </TableCell>
            </TableRow>
            <TableRow sx={{ '& > *': { borderBottom: 'unset' } }} >
                <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6} >
                    <Collapse in={open} timeout="auto" unmountOnExit>
                        <Box sx={{ margin: 1 }}>
                            <Typography color='primary' variant="h6" gutterBottom component="div" >
                                Detalhes <p></p>
                                {row.situacao === 'inativo' ? null :
                                    largura > 799 ?
                                        <>
                                            <Button sx={{ left: '82%' }} color='success' onClick={() => Editar(row)}>Editar</Button>
                                            <Button sx={{ left: '82%' }} color='error' onClick={() => DelUser(row)}>Apagar</Button>
                                        </>
                                        :
                                        <>
                                            <Button sx={{ left: '20%' }} color='success' onClick={() => Editar(row)}>Editar</Button>
                                            <Button sx={{ left: '20%' }} color='error' onClick={() => DelUser(row)}>Apagar</Button>
                                        </>
                                }
                            </Typography>
                            <Table sx={{ minWidth: 200 }} size={dense ? 'small' : 'medium'} aria-label="purchases" >
                                {largura > 799 ?
                                    <>
                                        <TableHead>
                                            <TableRow>
                                                <TableCell>Cep</TableCell>
                                                <TableCell>Rua</TableCell>
                                                <TableCell align="right">Numero</TableCell>
                                                <TableCell align="right">Complemento</TableCell>
                                                <TableCell align="right">Bairro</TableCell>
                                                <TableCell align="right">Cidade</TableCell>
                                                <TableCell align="right">UF</TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody >
                                            <TableRow >
                                                <TableCell component="th" scope="row">{row.cep}</TableCell>
                                                <TableCell>{row.logradouro}</TableCell>
                                                <TableCell align="center">{row.numero}</TableCell>
                                                <TableCell align="right">{row.complemento}</TableCell>
                                                <TableCell align="right">{row.bairro}</TableCell>
                                                <TableCell align="right">{row.cidade}</TableCell>
                                                <TableCell align="right">{row.uf}</TableCell>
                                            </TableRow>
                                        </TableBody>
                                    </>
                                    :
                                    <>
                                        <TableHead>
                                            <TableRow>
                                                <TableCell>Rua</TableCell>
                                                <TableCell component="th" scope="row">{row.logradouro}</TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody >
                                            <TableRow >
                                                <TableCell>Numero</TableCell>
                                                <TableCell>{row.numero}</TableCell>
                                            </TableRow>
                                        </TableBody>
                                        <TableHead>
                                            <TableRow>
                                                <TableCell>Cep</TableCell>
                                                <TableCell component="th" scope="row">{row.cep}</TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody >
                                            <TableRow >
                                                <TableCell>Complemento</TableCell>
                                                <TableCell>{row.complemento}</TableCell>
                                            </TableRow>
                                        </TableBody>
                                        <TableHead>
                                            <TableRow>
                                                <TableCell>Bairro</TableCell>
                                                <TableCell component="th" scope="row">{row.bairro}</TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody >
                                            <TableRow >
                                                <TableCell>Cidade</TableCell>
                                                <TableCell component="th" scope="row">{row.cidade}</TableCell>
                                            </TableRow>
                                        </TableBody>
                                        <TableHead>
                                            <TableRow>
                                                <TableCell>UF</TableCell>
                                                <TableCell>{row.uf}</TableCell>
                                            </TableRow>
                                        </TableHead>
                                    </>
                                }
                            </Table>
                        </Box>
                    </Collapse>
                </TableCell>
            </TableRow>
        </>
    )
}